        font-size: 0.9rem;
        margin-bottom: 0.8rem;
    }

    .hero-text p {
        font-size: 0.85rem;
        margin-bottom: 1.2rem;
    }

    .btn-new {
        padding: 0.6rem 1rem;
        font-size: 0.8rem;
    }

    .social-links {
        gap: 1rem;
    }

    .social-link {
        font-size: 1.1rem;
    }

    /* Keyboard keys extra small mobile */
    .key {
        width: 60px;
        height: 60px;
        font-size: 1.6rem;
        box-shadow: 0 10px 0 #102542, 0 15px 18px rgba(0,0,0,0.4);
    }

    .key:hover {
        transform: translateY(5px);
        box-shadow: 0 5px 0 #102542, 0 8px 12px rgba(0,0,0,0.5);
    }

    .key i, .key svg {
        font-size: 1.4rem;
    }

    .key svg {
        width: 22px;
        height: 22px;
    }

    .keyboard-container {
        transform: scale(0.7);
        width: 280px;
        height: 280px;
        margin: 1rem auto 1rem auto;
    }

    .skills-title {
        font-size: 1.2rem;
        margin-bottom: 1rem;
        font-weight: 700;
        color: #F87060;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    }

    .about-title-column h2 {
        font-size: 1.8rem;
    }

    .profile-photo {
        width: 120px;
        height: 120px;
    }

    .projects .section-header h2 {
        font-size: 1.6rem;
    }

    .github-chart-container img {
        transform: scale(0.7);
    }
}

/* Mobile Landscape Orientation */
@media (max-width: 768px) and (orientation: landscape) {
    .hero {
        min-height: 100vh;
        padding: 1rem 0;
    }

    .hero-container {
        min-height: 100vh;
        padding: 1rem 20px;
    }

    .hero-content {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
        align-items: center;
    }

    .hero-text {
        order: 1;
    }

    .hero-image {
        order: 2;
    }

    .hero-text h1 {
        font-size: 2rem;
    }

    .hero-text h2 {
        font-size: 1rem;
        margin-bottom: 1rem;
    }

    .hero-text p {
        font-size: 0.9rem;
        margin-bottom: 1.5rem;
    }

    .keyboard-container {
        transform: scale(0.75);
        width: 280px;
        height: 280px;
    }

    .skills-title {
        font-size: 1.3rem;
        margin-bottom: 1rem;
        font-weight: 700;
        color: #F87060;
        text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    }
}

/* Small Mobile Landscape */
@media (max-width: 480px) and (orientation: landscape) {
    .hero-content {
        gap: 1.5rem;
    }

    .hero-text h1 {
        font-size: 1.6rem;
        padding: 0.4rem 0.6rem;
    }

    .hero-text h2 {
        font-size: 0.9rem;
    }

    .hero-text p {
        font-size: 0.8rem;
    }

    .keyboard-container {
        transform: scale(0.65);
        width: 250px;
        height: 250px;
    }
}

#tech-art-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.about-description h3 {
    color: #F87060;
    font-size: 1.5rem;
}

/* Touch Device Optimizations */
@media (hover: none) and (pointer: coarse) {
    /* Miglioramenti per dispositivi touch */
    .nav-link {
        padding: 0.75rem 0;
        margin: 0.25rem 0;
    }
    
    .btn-new {
        min-height: 44px;
        touch-action: manipulation;
    }
    
    .social-link {
        min-width: 44px;
        min-height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        touch-action: manipulation;
    }
    
    .back-to-top-btn {
        min-width: 44px;
        min-height: 44px;
        touch-action: manipulation;
    }
    
    .github-link-main {
        min-height: 44px;
        touch-action: manipulation;
    }
    
    .key {
        touch-action: manipulation;
    }
    
    /* Rimuovi hover effects sui touch devices */
    .nav-link:hover,
    .social-link:hover,
    .btn-new:hover,
    .key:hover {
        transform: none;
    }
}

/* Performance optimizations */
.hero-content,
.about-grid,
.projects-container {
    will-change: transform;
}

/* Smooth scrolling migliorato */
@media (prefers-reduced-motion: no-preference) {
    html {
        scroll-behavior: smooth;
    }
}

/* Accessibilità */
@media (prefers-reduced-motion: reduce) {
    * {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
    }
    
    .keyboard {
        animation: none !important;
    }
    
    #matrix-canvas,
    #plexus-canvas {
        display: none !important;
    }
}

/* Prevent zoom on input focus (iOS Safari) */
@media screen and (-webkit-min-device-pixel-ratio: 0) {
    input,
    textarea {
        font-size: 16px !important;
    }
}

/* Miglioramenti specifici per iOS */
@supports (-webkit-touch-callout: none) {
    .nav-menu {
        -webkit-overflow-scrolling: touch;
    }
    
    .hero {
        -webkit-background-attachment: scroll;
    }
}

/* Miglioramenti per la navigazione con tastiera */
.nav-link:focus,
.btn-new:focus,
.social-link:focus,
.back-to-top-btn:focus,
.github-link-main:focus {
    outline: 2px solid #F87060;
    outline-offset: 2px;
}

/* Page Content Wrapper */
.page-content {
    margin-top: 80px;
    min-height: calc(100vh - 80px);
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
    /* Il sito è già in dark mode, quindi non servono cambiamenti */
}

/* High contrast mode support */
@media (prefers-contrast: high) {
    .hero-text h1 {
        background-color: rgba(0, 0, 0, 0.8);
    }
    
    .nav-menu {
        background-color: rgba(0, 0, 0, 0.95);
    }
    
    .btn-new {
        border: 2px solid #F87060;
    }
}

#neural-network-canvas, #data-flow-canvas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.5; /* Subtle background effect */
}